___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: white;
  background-color: #c5c3c3;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(90, 174, 248);
  height: 50px;
  color: black;
  position: relative;
}
nav .side-bar {
  position: absolute;
  top: 50px;
  left: 0;
  border-right: 1px black solid;
  background-color: #c5c3c3;
  height: 100vh;
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}
nav .side-bar h2 {
  text-decoration: underline;
}
nav .side-bar ul {
  list-style: none;
  text-align: center;
  padding: 0.5rem;
  margin-top: 0.5rem;
}
nav .side-bar ul li {
  padding: 0.5rem;
  border-bottom: solid 2px black;
}

.todo-list {
  margin: 0 auto;
  padding: 1rem;
  width: 60%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
  grid-template-rows: auto;
}
.todo-list .todo {
  background-color: black;
  color: white;
  padding: 1rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.todo-list .todo .todo-title {
  font-size: 1rem;
